import React from 'react';

import myLogo from '../../../assets/images/logo-JC.png';
import classes from './Logo.css';

const logo = (props) => (
    <div className={classes.Logo} style={{height: props.height}}>
        <a href='/'><img src={myLogo} alt="AP" /></a>
        <div>Jason Chen</div>
    </div>
);

export default logo;


