import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions';

import classes from './Article.css';
import Spinner from '../../components/UI/Spinner/Spinner';
import Aux from '../../hoc/Aux/Aux';
import iconClasses from '../../components/UI/iconfont/iconfont.css';

const article = props => {

    useEffect(() => {
        props.getDetail(props.match.params.id);
    }, [])

    const blogContent = { __html: props.content };

    if (props.title === '') {
        return <Spinner />
    } else {
        return (
            <Aux>
                <div className={classes.Content}>
                    <div className={classes.Button} onClick={() => {
                        window.history.back();
                    }}>
                        <span className={iconClasses.iconfont}>&#xe64b;</span>Back
                    </div>
                    <div className={classes.Title}>{props.title}</div>
                    <div dangerouslySetInnerHTML={blogContent} />
                </div>
            </Aux>
        )
    }

};

const mapState = (state) => ({
    title: state.article.get('title'),
    content: state.article.get('content')
});

const mapDispatch = (dispatch) => ({
    getDetail(id) {
        dispatch(actions.getDetail(id));
    }
});

export default connect(mapState, mapDispatch)(article);


