import React from 'react';

import myResume from '../../assets/JasonChen.pdf';
import Aux from '../../hoc/Aux/Aux';
import classes from './About.css';

const about = () => (
    <Aux>
        <div className={classes.Content}>
            <div>
                <h1>About Me</h1>
                <blockquote>
                    <p><strong>WE ARE INFINITE!</strong></p>
                </blockquote>
                <p>
                    Hi I'm Jason Chen and this is where I will be writing down my random thoughts that can be anything.
                    This is my first attempt at a blog as well as website design. As a starter I apologize in advance for my
                    writing and design skills.
                    </p>
                <p>
                    Five years ago, I came to New Zealand - Home of Middle‑earth. This journey was a chance for me to explore this country
                    also myself. That was the time I decided to jump into IT after my graduation in economics. Honestly, it seems like a huge
                    move. But why not if you are into something? Like what my favourite qoute says, we are <strong>INFINITE</strong>.
                </p>
                <p>
                    Currently I'm actively looking for a web development position to contribute myself in. If you've got a match opportunity that I
                    can sparkle on. Please let me know.
                    </p>
                <p>I appreciate your attention.</p>
                <ul>
                    <li>Check out my<a href={myResume} target="_blank" >Resume</a></li>
                    <li>Wanna talk?<a href="mailto:jason.chen940501@gmail.com" >Leave me a message!</a></li>
                </ul>
            </div>
        </div>
    </Aux>
);

export default about;

