import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../store/actions';
import { Link } from 'react-router-dom';

import classes from './Blog.css';
import Spinner from '../../components/UI/Spinner/Spinner';
import Aux from '../../hoc/Aux/Aux';

const blog = props => {

    useEffect(() => {
        props.getBlogList();
    }, [])

    if (props.list.length === 0) {
        return <Spinner />
    } else {
        return (
            <Aux>
                <div className={classes.Content}>
                    {
                        props.list.map((item, id) => (
                            <Link key={item.get('id')} to={'/Blog/' + item.get('id')}>
                                <div className={classes.Article}>
                                    <div className={classes.Title}>{item.get('title')}</div>
                                    <div className={classes.Desc}>{item.get('desc')}</div>
                                </div>
                            </Link>
                        ))
                    }
                </div>
            </Aux>
        )
    }

};

const mapState = (state) => ({
    list: state.blog.get('articleList')
});

const mapDispatch = (dispatch) => ({
    getBlogList() {
        dispatch(actions.getBlogList());
    }
});

export default connect(mapState, mapDispatch)(blog);


