import React, { Component } from 'react';
import { Route, Switch, BrowserRouter, Redirect } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';
import './App.css';

import Layout from './hoc/Layout/Layout';
import homePage from './containers/HomePage';
import about from './containers/About';
import blog from './containers/Blog';
import myMessage from './containers/Message';
import article from './containers/Article';
import myProjects from './containers/Projects';

// import Toolbar from './components/Navigation/Toolbar/Toolbar';
// import ItemCard from './components/ItemCard/ItemCard';

let routes = (
    <Switch>
      <Route path="/Blog/:id" exact component={article} />
      <Route path='/Blog' exact component={blog} />
      <Route path="/Message" exact component={myMessage} />
      <Route path="/About" exact component={about} />
      <Route path="/Lab" exact component={myProjects} />
      <Route path="/" exact component={homePage} />
      <Redirect to="/" />
    </Switch>
  );

class App extends Component {

  render() {
    return (
      <Provider store={store} >
        <BrowserRouter>
          <Layout>{routes}</Layout>
        </BrowserRouter>
      </Provider>
    );
  }
};

export default App;



